body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    background-color: darkcyan;
    color: white;
  }
  
  .wrapper{
    max-width: 900px;
    margin: auto;
    text-align: center;
    margin-top: 250px;
    font-family: 'monospace';
  }
  #title{
    font-size: 40px;
    font-family: 'Courier New';
  }
  #settings{
    font-size: 30px;
    margin-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-family: 'Courier New';
  }
  .options{
    display: -webkit-flex;
    display: flex;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      -webkit-align-items: center;
              align-items: center;
  }
  #control-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 25px;
  }
  #time-wrapper{
    font-size: 35px;
  }
  #display{
    border: 4px solid lightseagreen;
    width: 300px;
    margin: auto;
    margin-top: 65px;
    border-radius: 15px;
  }
  .btn{
    font-size: 15px;
  }
  .btn:hover{
    cursor: pointer;
  }
  #start_stop{
    margin-right: 15px;
  }
  #start_stop:hover{
    cursor: pointer;
  }
  #reset:hover{
    cursor: pointer;
  }
  #start_stop:active, #reset:active, .btn:active{
    color: lightseagreen;
  }
  @media only screen and (max-width: 600px){
    #settings{
      -webkit-flex-direction: column;
              flex-direction: column;
    }
    #break{
      margin-bottom: 40px;
    }
    .options{
      -webkit-justify-content: center;
              justify-content: center;
    }
    #break-length, #session-length{
      margin-left: 15px;
      margin-right: 10px;
    }
  }
  @media only screen and (max-width: 430px){
    .wrapper{
      margin-top: 60px;
    }
  }
  @media only screen and (max-width: 350px){
    .wrapper{
      margin-top: 40px;
    }
  }
